export function getBoundingRelativeRect(el: HTMLElement, refEl?: HTMLElement | null) {
  const ref = refEl || document.body;
  const elRect = el.getBoundingClientRect();
  const refElRect = ref.getBoundingClientRect();
  const left = elRect.left - refElRect.left;
  const top = elRect.top - refElRect.top;
  const { width, height } = elRect;

  return {
    left,
    top,
    right: left + width,
    bottom: top + height,
    width,
    height,
  };
}

export function getRelativeMouseEvent(
  event: React.MouseEvent | MouseEvent,
  refEl: HTMLElement,
) {
  const { clientX, clientY } = event;
  const refElRect = refEl.getBoundingClientRect();
  const relativeX = clientX - refElRect.left;
  const relativeY = clientY - refElRect.top;

  return {
    ...event,
    clientX,
    clientY,
    relativeX,
    relativeY,
  };
}

export function getRelativeTouchEvent(touch: React.Touch | Touch, refEl: HTMLElement) {
  const { clientX, clientY } = touch;
  const refElRect = refEl.getBoundingClientRect();
  const relativeX = clientX - refElRect.left;
  const relativeY = clientY - refElRect.top;
  return {
    clientX,
    clientY,
    relativeX,
    relativeY,
  };
}

export type RelativeEventType = ReturnType<typeof getRelativeMouseEvent> | ReturnType<typeof getRelativeTouchEvent>;
